*, *::before, *::after {
    box-sizing: border-box;
}

body {
    @include text(15px, $color: $black, $line-height: 1.5);
    font-family: "Raleway", sans-serif;
    background-color: $light-grey;
}

// Make background color consistent on different browsers.
select {
    background-color: $white;
}

